body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
  margin: 0;
  padding: 0;
}
.header{
padding: 20px 100px;
background-color:gray;
color: white;
display: flex;
justify-content: space-between;
}
.cart-icon .counter{
  right:7px;
  top:-6px;
  position: relative;
  display:flex;
  justify-content: center;
  align-items: center;
  height: 18px;
  width: 18px;
  background-color: blue;
  border-radius: 50%;
  
}
.total{
  /* box-sizing: border-box; */
  background-color:grey;
  padding: 20px;
  position:fixed;
  bottom:0px;
  height:15px;
  width:100%;
  padding-left: 91vw;
  font-size: 20px;
  
}
.cart-icon{
  display: flex;
}
.icon{
  
  font-size: 25px;
}
.cart-box{
  box-sizing: border-box;
  border-radius: 10px;
  background-color: aliceblue;
  box-shadow: 0 2px #ccc;
  display:inline-flex;
  justify-content: center;
  align-items:center;
  gap:100px;
  flex-direction: row;
  box-shadow:0px 0px 7px black;
  margin:30px;
  width:500px;
  height:300px;
}
.img-wrapper{
  /* margin: 30px;  */
  padding:10px;
  width:180px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-right:1px solid rgb(224, 218, 218);
}

img{
  height:200px;
}

.content{
  padding:10px;
  display:flex;
  flex-direction:column;
  flex-grow: 1;
  gap:10px;
}
.price{
  margin-bottom: auto;
  font-size: 30px;
  
}

.cart-box button{
background-color: black;
color:white;
border-radius:3px;
height:30px;
}

.quantity{
 display:flex;
 align-self:flex-end;
 border-radius: 3px;
 border:1px solid gray;
 
}
.quantity div{
  height:20px;
  width:20px;
  display:flex;
  align-items:center;
  justify-content:center;
}
.quantity>div:first-child,.quantity>div:last-child{
  background-color: orange;
}
.quantity button{
  background-color: aqua;
  height:20px;
  width:20px;

}

main{
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  min-height: 80vh;
  align-content: center;
  gap:50px;
}